import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
// import Type from '@/components/Content/Type/Type'
import Boyfriend from '@/components/Content/Type/Boyfriend'
import Cloth from '@/components/Content/Type/Cloth'
import Internal from '@/components/Content/Type/Internal'
import Motion from '@/components/Content/Type/Motion'
import Package from '@/components/Content/Type/Package'
import Shoes from '@/components/Content/Type/Shoes'
import Snacks from '@/components/Content/Type/Snacks'
import Water from '@/components/Content/Type/Water'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      children: [
        // {
        //   path: 'type',
        //   component: Type
        // },
        {
          path: 'cloth',
          component: Cloth
        },
        {
          path: '',
          redirect: 'cloth'
        },
        {
          path: 'boyfriend',
          component: Boyfriend
        },
        {
          path: 'internal',
          component: Internal
        },
        {
          path: 'motion',
          component: Motion
        },
        {
          path: 'package',
          component: Package
        },
        {
          path: 'shoes',
          component: Shoes
        },
        {
          path: 'snacks',
          component: Snacks
        },
        {
          path: 'water',
          component: Water
        }
      ]
    }
  ]
})



