<template>
  <div class="type">
    <div class="goods" v-for="(go, i) in gos" :key="i">
      <a class="lin-img" href="javascript:;">
        <div class="sales-num">{{go.num}}</div>
        <img class="img" :src='go.img' alt="">
      </a>
      <a class="intr" href="javascript:;">{{go.title}}
      </a>
      <a class="price" href="javascript:;">
        <div class="much">
          <span class="new-price">{{go.price1}}</span>
          <span class="new-price1">{{go.price2}}</span>
          <span class="old-price">{{go.price3}}</span>
        </div>
      </a>
      <a class="btn" href="javascript:;">立即购买</a>
    </div>
  </div>
</template>

<script>
export default {
  props: ['gos']
}
</script>

<style scoped>
  a{
    text-decoration: none;
    display: inline-block;
    font-size: 12px;
  }
  .type{
    width: 960px;
    margin: 0 auto;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
  }
  .goods{
    width: 230px;
    background-color: #fff;
    font-size: 0px;
    padding: 5px 0 10px 0;
    margin: 0 5px 20px;
  }
  .goods .lin-img{
    width: 220px;
    margin: 0 5px;
    position: relative;
  }
  .img{
    width: 220px;
    height: 330px;
  }
  .sales-num{
    position: absolute;
    bottom: 15px;
    right: 0px;
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 13px;
    padding: 2px 10px;
    color: #fff;
  }
  .intr{
    width: 220px;
    margin: 0 5px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 7px 0 3px 0;
    color: #444;
    font-size: 16px;
  }
  .much{
    width: 214px;
    margin: 0 7px;
  }
  .much .new-price{
    border: 1px solid rgba(255, 0, 85, 0.719);
    padding: 2px;
    border-radius: 5px;
    color: rgba(255, 0, 85, 0.719);
    position: relative;
    top: -5px;
  }
  .much .new-price1{
    font-size: 20px;
    color: #333;
  }
  .much .old-price{
    text-decoration: line-through;
    color: #666;
  }
  .btn{
    width: 220px;
    /* height: 36px; */
    line-height: 22px;
    font-size: 16px;
    color: #fff;
    background-color: #FF3377;
    background-image: url(http://s17.mogucdn.com/p1/150910/upload_iezdqojumyyggzbygmzdambqmmyde_12x21.png);
    background-repeat: no-repeat;
    background-position: 150px 8px;
    background-size: 10px;
  }
</style>



