<template>
  <div class="content">
    <router-link class="rou" :to="lin.type" exact v-for="(lin, i) in lins" :key="i">
      <span class="span-text">{{lin.text}}</span>
    </router-link>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  props: ['links'],
  data () {
    return {
      lins: [
        {
          type: 'cloth',
          text: '新秋装'
        },
        {
          type: 'shoes',
          text: '新秋鞋'
        },
        {
          type: 'water',
          text: '秋冬补水'
        },
        {
          type: 'boyfriend',
          text: '男友'
        },
        {
          type: 'motion',
          text: '运动装备'
        },
        {
          type: 'package',
          text: '新款包包'
        },
        {
          type: 'internal',
          text: '内搭打底'
        },
        {
          type: 'snacks',
          text: '养生零食'
        }
      ]
    }
  }
}
</script>

<style scoped>
  .rou{
    color: #fff;
  }
  .router-link-active{
    /* color: red; */
    color: #FF6494;
  }
  .router-link-active .span-text{
    background-color: #fff;
  }
  .content{
    width: 960px;
    margin: 0 auto;
  }
  .span-text{
    display: inline-block;
    width: 239px;
    height: 54px;
    line-height: 54px;
    background-color: #FF6494;
    border: 1px solid #fff;
  }
</style>



